<template>
  <footer v-show="total">
    <input type="checkbox" :checked="isAll" @change="checkAll" />
    <span>已完成 {{ doneTotal }} / 全部 {{ total }} 项</span>
    <button class="deleteAll" @click="clearAll">清除已完成</button>
  </footer>
</template>

<script>
export default {
  name: "MyFooter",
  props: ["todos", "checkAllToDo", "deleteDone"],
  computed: {
    doneTotal() {
      return this.todos.filter((todo) => todo.isDone).length;
    },
    total() {
      return this.todos.length;
    },
    isAll() {
      if (this.doneTotal === this.total && this.total) {
        return true;
      } else {
        return false;
      }
    },
  },
  methods: {
    checkAll(e) {
      this.checkAllToDo(e.target.checked);
    },
    clearAll() {
      this.deleteDone()
    },
  },
};
</script>

<style>
footer {
  height: 30px;
  line-height: 30px;
  border: 1px solid #ccc;
  border-radius: 2px;
}
</style>
