<template>
  <header>
      <input 
        type="text" 
        placeholder="输入待办，回车以添加" 
        v-model="title" 
        @keyup.enter="addData">
    </header>
</template>

<script>
import {nanoid} from 'nanoid'
export default {
  name: "MyHeader",
  data() {
    return {
      title: "",
    };
  },
  methods: {
    addData(e) {
      const todo = {
        id: nanoid(),
        content: e.target.value, 
        isDone: false,
      };
      this.addTodo(todo);
      e.target.value = "";
    },
  },
  props: ["addTodo"]
};

</script>

<style scoped>

input {
  width: 100%;
  height: 30px;
  border: 1px solid #ccc;
  border-radius: 2px;
}
input:focus {
  outline: none;
}

</style>