<template>
  <li>
    <label>
      <input type="checkbox" :checked="todo.isDone" @change="handleCheck(todo.id)" />
      <span>{{ todo.content }}</span>
    </label>

    <button @click="handleDelete(todo.id)">删除</button>
  </li>
</template>

<script>
export default {
  name: "MyItem",
  props: ["todo", "checkTodo", "myDelete"],
  methods: {
    handleCheck(id) {
      this.checkTodo(id);
    },
    handleDelete(id) {
      this.myDelete(id);
    },
  },
};
</script>

